import classes from "./VideoBackDrop.module.scss";

function VideoBackDrop({
  closeBackDrop,
  activeId,
  videoUrls,
  previousVideo,
  nextVideo,
  setMouseOnArrow,
}) {
  return (
    <div className={classes.backDrop} onClick={closeBackDrop}>
      <iframe
        style={{ top: "50vh", left: "50vw" }}
        width="905"
        height="509"
        src={videoUrls[activeId]}
        title="YouTube video player"
        frameborder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        allowfullscreen
      ></iframe>
      <div
        className={classes.exitButton}
        style={{
          maskImage: "url('/Icons/exitIcon.png')",
          webkitMaskImage: "url('/Icons/exitIcon.png')",
        }}
      ></div>
      <div
        className={classes.previousButton}
        onMouseOver={() => setMouseOnArrow(true)}
        onMouseOut={() => setMouseOnArrow(false)}
        onClick={previousVideo}
        style={{
          maskImage: "url('/Icons/newLeft.png')",
          webkitMaskImage: "url('/Icons/newLeft.png')",
        }}
      ></div>
      <div
        className={classes.nextButton}
        onMouseOver={() => setMouseOnArrow(true)}
        onMouseOut={() => setMouseOnArrow(false)}
        onClick={nextVideo}
        style={{
          maskImage: "url('/Icons/newRight.png')",
          webkitMaskImage: "url('/Icons/newRight.png')",
        }}
      ></div>
    </div>
  );
}

export default VideoBackDrop;
