import classes from "./recordings.module.scss";
import Banner from "../Banner/Banner";
import VideoPreview from "./VideoPreview";
import VideoBackDrop from "./VideoBackDrop";
import { useState } from "react";
import Contact from "../Contact/Contact";
import NoamLogo from "../NoamLogo/NoamLogo";

const videoUrls = [
  "https://www.youtube.com/embed/VB932i5AEPM",
  "https://www.youtube.com/embed/pmUs88HvuhM",
  "https://www.youtube.com/embed/BacBZ_iWF-g",
  "https://www.youtube.com/embed/VB932i5AEPM",
  "https://www.youtube.com/embed/VB932i5AEPM",
  "https://www.youtube.com/embed/pmUs88HvuhM",
  "https://www.youtube.com/embed/VB932i5AEPM",
  "https://www.youtube.com/embed/pmUs88HvuhM",
  "https://www.youtube.com/embed/BacBZ_iWF-g",
];

const videosData = [
  [
    {
      imageName: 1,
      id: 0,
      text: "random piece",
    },
    {
      imageName: 3,
      id: 1,
      text: "random piece ",
    },
    {
      imageName: 4,
      id: 2,
      text: "random piece",
    },
  ],
  [
    {
      imageName: 1,
      id: 3,
      text: "random piece",
    },
    {
      imageName: 2,
      id: 4,
      text: "random piece ",
    },
    {
      imageName: 3,
      id: 5,
      text: "random piece ",
    },
  ],
  [
    {
      imageName: 2,
      id: 6,
      text: "random piece ",
    },
    {
      imageName: 3,
      id: 7,
      text: "random piece ",
    },
    {
      imageName: 4,
      id: 8,
      text: "random piece ",
    },
  ],
];

function Recordings() {
  const [backDrop, setBackDrop] = useState(false);
  const [activeId, setActiveId] = useState(-1);
  const [mouseOnArrow, setMouseOnArrow] = useState(false);

  function previewClicked(id) {
    setBackDrop(true);
    setActiveId(id);
  }

  function closeBackDrop() {
    if (!mouseOnArrow) {
      setBackDrop(false);
    }
  }
  function previousVideo() {
    setActiveId((activeId - 1 + videoUrls.length) % videoUrls.length);
  }

  function nextVideo() {
    setActiveId((activeId + 1) % videoUrls.length);
  }

  let galleryElement = videosData.map((row) => {
    return (
      <div className={classes.row1}>
        {row.map(({ imageName, id, text }) => {
          return (
            <VideoPreview
              imageName={"/pictures/" + imageName + ".jpeg"}
              id={id}
              previewClicked={previewClicked}
              text={text}
            />
          );
        })}
      </div>
    );
  });

  return (
    <div className={classes.recordings}>
      <Banner
        bgImage="/pictures/recordings.jpg"
        mainHeader="Recordings"
        secondHeader="Leikie"
      />
      <div className={classes.textWrapper}>{galleryElement}</div>
      <div className={classes.contactBackDrop}>
        <NoamLogo />
        <Contact />
      </div>
      {backDrop ? (
        <VideoBackDrop
          previousVideo={previousVideo}
          nextVideo={nextVideo}
          closeBackDrop={closeBackDrop}
          activeId={activeId}
          videoUrls={videoUrls}
          setMouseOnArrow={setMouseOnArrow}
        />
      ) : null}
    </div>
  );
}

export default Recordings;
