import { useState } from "react";
import Banner from "../Banner/Banner";
import NoamLogo from "../NoamLogo/NoamLogo";
import Contact from "../Contact/Contact";
import { Formik, Form, Field, ErrorMessage } from "formik";
import InputField from "./InputField";
import classes from "./ContactForm.module.scss";

function ContactForm() {
  return (
    <div className={classes.home}>
      {/* <ThumbnailGallery /> */}
      <Banner
        bgImage="/pictures/Leikie3.jpg"
        mainHeader="Leikie Glick"
        secondHeader="Contact"
      />
      <Formik
        initialValues={{ firstName: "", lastName: "", email: "", message: "" }}
        validate={(values) => {
          const errors = {};
          if (!values.email) {
            errors.email = "Required";
          } else if (
            !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
          ) {
            errors.email = "Invalid email address";
          }
          return errors;
        }}
        onSubmit={(values, { setSubmitting }) => {
          setTimeout(() => {
            alert(JSON.stringify(values, null, 2));
            setSubmitting(false);
          }, 400);
        }}
      >
        {({ isSubmitting }) => (
          <Form className={classes.wrapper}>
            <div className={classes.inputNames}>
              <InputField
                name="firstName"
                title="First Name"
                width={47.5}
                height={50}
                placeholder="Noam"
              />
              <InputField
                name="lastName"
                title="Last Name"
                width={47.5}
                height={50}
                placeholder="Shochat"
              />
            </div>
            <InputField
              name="email"
              title="Email"
              width={100}
              height={50}
              placeholder="Noamshochat18@gmail.com"
            />
            <InputField
              name="message"
              title="Message"
              width={100}
              height={200}
              placeholder="write your message here!"
            />
            <button
              className={classes.button}
              type="submit"
              disabled={isSubmitting}
            >
              Submit
            </button>
          </Form>
        )}
      </Formik>
      <div className={classes.contactBackDrop}>
        <NoamLogo />
        <Contact />
      </div>
    </div>
  );
}

export default ContactForm;
