import classes from "./Banner.module.scss";
import { useEffect, useState } from "react";

function Banner({ mainHeader, secondHeader, bgImage }) {
  const [offSet, setOffSet] = useState(0);

  useEffect(() => {
    window.onscroll = () => {
      setOffSet(Math.floor(window.pageYOffset));
    };
  }, []);

  let backgroundPosition = `0% ${offSet / 10}% `;
  let mainHeaderTop = "10%" + offSet / 2;
  let secondHeaderTop = "20%" + offSet / 2;

  return (
    <div
      className={classes.backgroundImage}
      style={{
        backgroundImage: "url(" + bgImage + ")",
        backgroundPosition: backgroundPosition,
      }}
    >
      <div className={classes.mainHeader} style={{ top: mainHeaderTop }}>
        {mainHeader}
      </div>
      <div className={classes.secondHeader} style={{ top: secondHeaderTop }}>
        {secondHeader}
      </div>
    </div>
  );
}
export default Banner;

