import React from "react";
import classes from "./Gallery.module.scss";
import Carousel from "react-gallery-carousel";
import Banner from "../Banner/Banner";
import Contact from "../Contact/Contact";
import NoamLogo from "../NoamLogo/NoamLogo";
import "react-gallery-carousel/dist/index.css";

const data = require("./pictures.json").data;

function Gallery() {
  const images = data.map(({ url, width, height }) => ({
    src: url,
  }));
  return (
    <div className={classes.wrapper}>
      <Banner
        bgImage="/pictures/Leikie6.jpg"
        mainHeader="Leikie Glick"
        secondHeader="Violist"
      />
      <div className={classes.carouselWrapper}>
        <Carousel
          images={images}
          isAutoPlaying={true}
          objectFit="contain"
          objectFitAtMax="contain"
          style={{ height: 600, width: 900 }}
        />
      </div>

      <div className={classes.contactBackDrop}>
        <NoamLogo />
        <Contact />
      </div>
    </div>
  );
}

export default Gallery;
